/*ANIMATIONS TRANSITIONS DE PAGE*/
.slide-left-enter-active,
.slide-left-leave-active,
.slide-right-enter-active,
.slide-right-leave-active {
  -webkit-transition: all 0.75s ease-out;
  transition: all 0.75s ease-out;
  overflow: hidden;
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(100vw, 0);
          transform: translate(100vw, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 1;
  -webkit-transform: translate(-100vw, 0);
          transform: translate(-100vw, 0);
}
.slide-top-enter-active,
.slide-top-leave-active,
.slide-bottom-enter-active,
.slide-bottom-leave-active {
  -webkit-transition-duration: 0.5s, 0.5s, 0.5s;
          transition-duration: 0.5s, 0.5s, 0.5s;
  -webkit-transition-property: height, opacity, -webkit-transform;
  transition-property: height, opacity, -webkit-transform;
  transition-property: height, opacity, transform;
  transition-property: height, opacity, transform, -webkit-transform;
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
  overflow: hidden;
}
.slide-top-enter,
.slide-bottom-leave-active {
  opacity: 0;
  -webkit-transform: translate(0, 100vh);
          transform: translate(0, 100vh);
}
.slide-top-leave-active,
.slide-bottom-enter {
  opacity: 0;
  -webkit-transform: translate(0, -100vh);
          transform: translate(0, -100vh);
}
/*APP*/
html{
  height: 100vh;
}
#app {
  font-family: MAXWELL REGULAR, Helvetica, Arial, sans-serif;
